<template>
  <div class="product-card">
    <img :src="image" :alt="name" class="product-image" />
    <h2>{{ name }}</h2>
    <p>{{ description }}</p>
    <p class="price">{{ price }} –≥—Ä–Ω</p>
    <button class="buy-button" @click="handleAddToCart">–ö—É–ø–∏—Ç–∏</button>
  </div>
</template>

<script>
import { inject } from "vue";

export default {
  props: {
    name: String,
    price: Number,
    image: String,
    description: {
      type: String,
      default: "–û–ø–∏—Å —Ç–æ–≤–∞—Ä—É –≤—ñ–¥—Å—É—Ç–Ω—ñ–π",
    },
  },
  setup(props) {
    const addToCart = inject("addToCart");

    const handleAddToCart = () => {
      const product = {
        name: props.name,
        price: props.price,
        image: props.image,
      };
      addToCart(product);
    };

    return {
      handleAddToCart,
    };
  },
};
</script>

