  height: 200px; /* –æ–¥–Ω–∞–∫–æ–≤–∞ –≤–∏—Å–æ—Ç–∞ –¥–ª—è –≤—Å—ñ—Ö */
  object-fit: cover; /* –æ–±—Ä—ñ–∑–∞—î –∑–æ–±—Ä–∞–∂–µ–Ω–Ω—è –±–µ–∑ —Å–ø–æ—Ç–≤–æ—Ä–µ–Ω—å */
  border-radius: 8px; /* –æ–ø—Ü—ñ–æ–Ω–∞–ª—å–Ω–æ: –æ–∫—Ä—É–≥–ª–µ–Ω–Ω—è –∫—É—Ç—ñ–≤ */
  display: block;
}

